@import url(https://fonts.google.com/css2?family=Monsterrate&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

*{
  box-sizing: border-box;
  margin:0;
  padding: 0;
  font-family: 'Monsterrate', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff
}

.coin-search{
  margin-bottom: 4px;
  display:flex;
  flex-direction: column;
  align-items: center;
}

.coin-input{
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  background-image: linear-gradient(
    -225deg, 
    #ac32e4 0%,
    #7918f2 40%,
    #4801ff 100%,
  );

  color: #e2e2e2;

}

.coin-input::-webkit-input-placeholder {
  color: e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: e2e2e2;
}

.coin-input::placeholder {
  color: e2e2e2;
}

@media screen and (max-width: 390px) {
  
    .coin-text{
      font-size: 13px;
    }
    form {
      width: 80px;
    }
    .coin-app{
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 10px;
      margin-right: 10%;
      margin-left: 10%;
      color: #fff;
      box-sizing: border-box;
      margin:0;
      padding: 0;
      font-family: 'Monsterrate', sans-serif;
      background-color: #1a1a1c;
      color: #fff;
    }
    
    .coin-search{
      margin-bottom: 10px;
      display:flex;
      flex-direction: column;
      align-items: center;
    }
    
    .coin-input{
      margin-top: 10px;
      padding-left: 5%;
      width: 100%;
      height: 10%;
      border-radius: 4%;
      background-image: linear-gradient(
        -225deg, 
        #ac32e4 0%,
        #7918f2 40%,
        #4801ff 100%,
      );
    
      color: #e2d6d6;
    
    }
    
    .coin-input::-webkit-input-placeholder {
      color: e2e2e2;
    }
    
    .coin-input:-ms-input-placeholder {
      color: e2e2e2;
    }
    
    .coin-input::placeholder {
      color: e2e2e2;
    }
    
  }

.coin-cointainer {
    display:flex;
    justify-content: center;

}

.coin-row{
    display:flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7dd;
    width: 95%;
}

.coin{
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;

}

.coin > h1 {
    font-size: 16px;
    width: 150px
}

.coin-symbol{
    text-transform: uppercase;
}

.coin-data{
    display:flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;

}

.coin-image{
    height: 30px;
    padding-right: 15px;
}

.coin-price{
    width: 110px;
    
}

/* 110 */

.coin-volume{
    width: 230px;
}

/*230*/

.coin-market-cap{
    padding-left: 155px;
}

/* 155px */

.coin-percent{
    width: 100%;
}

.red{
    color: #f00606;
}

.green{
    color: #11d811
}


@media screen and (max-width: 400px) {

    p{
        font-size: 8px;
    }

    .coin-cointainer {
        display:flex;
        justify-content: center;
    
    }
    
    .coin-row{
        display:flex;
        flex-direction: row;
        justify-items: start;
        align-items: center;
        height: 40px;
        border-bottom: 1px solid #d7d7dd;
        width: 95%;
    }
    
    .coin{
        display: flex;
        align-items: center;
        padding-right: 24px;
        max-width: 40px;
    
    }
    
    .coin > h1 {
        font-size: 8px;
        width: 42px; 
        margin-left: 150px;
        margin-right: -40px
        
    }

    .coin > img {

       margin-left: 31px;

    }

    .coin-image {
        margin-left: 100px;
    }
    
    .coin-symbol{
        text-transform: uppercase;
        margin-left: 50px;
        margin-right: 220px;
    }
    
    .coin-data{
        display:flex;
        text-align: center;
        justify-content: space-between;
        width: 100%;
    
    }
    
    .coin-image{
        height: 30px;
        padding-right: 1px;
        margin-right: -150px;
        margin-left: 90px;
    }
    
    .coin-price{
        margin-left: -170px;
        margin-right: -90px;
    }
    
    /* 110 */
    
    .coin-volume{
       width: 30px;
       margin-left: -90px; 
       margin-right: 59px;
    }
    
    /*230*/
    
    .coin-market-cap{
        padding-left: 5px;
        font-size: 8px;
        margin-right: 70px;
    }
    
    /* 155px */
    
    .coin-percent{
       width: 100%; 
    }
    
    .red{
        color: #f00606;
        margin-left: -28px;
    }
    
    .green{
        color: #11d811;
        margin-left: -28px;
    }
   
  }
